import React, { useEffect, useState } from "react";
import { TableBody, TableHead, TableRow, TableCell } from '@material-ui/core'

import { getCars, getCategorySelects, setCurrentFilters, setCurrentPage } from "../../store/slices/cars/cars-slice"
import { useSelector, useDispatch } from 'react-redux';
import { RootState } from '../../store/store';

import CarRow from '../car-row/car-row';
import AbstractTable from '../abstract-table/abstract-table'

import { TCarsList } from '../../store/slices/cars/types';
import { setSelectsOptions, getMappedSelects } from '../../globals/utils'


const TABLE_NAME = '–°–ø–∏—Å–æ–∫ –∞–≤—Ç–æ–º–æ–±–∏–ª–µ–π'
const LIMIT = 7
const QUERY_SELECTOR = ''


const carsTable: React.FC = () => {
    const dispatch = useDispatch()

    const page = useSelector((state: RootState) => state.cars.page)
    const filterData = useSelector((state: RootState) => state.cars.filters)
    const currentCars = useSelector((state: RootState) => state.cars.carsList)
    const carsCount = useSelector((state: RootState) => state.cars.carsCount)

    const categorySelects = useSelector((state: RootState) => state.cars.categorySelects)

    const [isLoading, setIsLoading] = useState(false)

    const carsSelects = [

        {
            name: 'categoryId',
            options: setSelectsOptions(categorySelects, '–í—Å–µ –∫–∞—Ç–µ–≥–æ—Ä–∏–∏')
        },
    ]

    const selectMap = getMappedSelects(categorySelects)

    const init–°ars = async () => {
        setIsLoading(true)

        await Promise.all([
            (async () => await dispatch(getCars({ page: page, limit: LIMIT, filters: filterData })))(),
            (async () => await dispatch(getCategorySelects()))(),
        ])

        setIsLoading(false)
    }

    useEffect(() => {
        init–°ars()
    }, [page, filterData])


    const getTableCars = (cars: TCarsList) => {
        return <>
            <TableHead>
                <TableRow>
                    <TableCell align="center">–§–æ—Ç–æ</TableCell>
                    <TableCell align="center">–ù–∞–∑–≤–∞–Ω–∏–µ</TableCell>
                    <TableCell align="center">–ö–∞—Ç–µ–≥–æ—Ä–∏—è</TableCell>
                    <TableCell align="center">–ú–∏–Ω.—Ü–µ–Ω–∞</TableCell>
                    <TableCell align="center">–ú–∞–∫—Å.—Ü–µ–Ω–∞</TableCell>
                    <TableCell align="center">–û–ø–∏—Å–∞–Ω–∏–µ</TableCell>
                    <TableCell align="center">–¶–≤–µ—Ç–∞</TableCell>
                </TableRow>
            </TableHead>
            <TableBody>
                {cars.map((car) => {
                    return <CarRow key={car.id} carData={car} />
                })}
            </TableBody>
        </>
    }

    return (
        <AbstractTable
            name={TABLE_NAME}
            isLoading={isLoading}
            querySelector={QUERY_SELECTOR}
            dataList={currentCars}
            dataCount={carsCount}
            limit={LIMIT}
            selectData={carsSelects}
            selectMap={selectMap}
            getTableCallback={getTableCars}
            setCurrentFilters={setCurrentFilters}
            setCurrentPage={setCurrentPage}
        />
    );
}

export default carsTable